<template>
    <div class="location">
        <div class="left"  @click="clickleft">
            <div class="left-one rect" :class="{active : num === 1}"></div>
            <svg version="1.1" class="svglocation" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px"
            width="50px" height="50px" viewBox="0 0 50 50" enable-background="new 0 0 50 50" xml:space="preserve">
                <line fill="#FFFFFF" stroke="#000000" stroke-width="0.5" stroke-miterlimit="10" x1="24.75" y1="15.75" x2="24.75" y2="25.245"/>
                <circle cx="24.75" cy="27.747" r="2.503"/>
            </svg>
        </div>
        <div class="right" @click="clickright">
            <div class="right-one rect" :class="{active : num === 1}"></div>
            <svg version="1.1" class="rightsvg" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px"
            width="50px" height="50px" viewBox="0 0 50 50" enable-background="new 0 0 50 50" xml:space="preserve">
                <rect x="18.216" y="21" width="13.75" height="1" fill="#FFFFFF"/>
                <rect x="18.216" y="24" width="13.75" height="1" fill="#FFFFFF"/>
                <rect x="18.216" y="27" width="13.75" height="1" fill="#FFFFFF"/>
            </svg>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
export default {
    props: {

    },
    data() {
        return {
            routerflag: true,
            num: 1
        };
    },
    computed: {
        change() {
            
        }
    },
    created() {

    },
    mounted() {

    },
    watch: {

    },
    methods: {
        clickleft() {
            if(this.routerflag){
            this.$router.push('address')
            this.routerflag = !this.routerflag
            } else {
                this.$router.go(-1)
                this.routerflag = !this.routerflag
            }
        },
        clickright() {
            if(this.routerflag){
            this.$router.push('productList')
            this.routerflag = !this.routerflag
            } else {
                this.$router.go(-1)
                this.routerflag = !this.routerflag
            }
        }
    },
    components: {

    },
};
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
    .location
        position: fixed
        top: 20px
        left: 20px
        display: flex
        z-index: 200
        .left
            position: relative
            width: 50px
            height: 50px
            .rect
                position: absolute 
                top: 0
                left: 0
                bottom: 0
                background: grey 
                transition: .3s all ease
            .active
                right: 0
            .svglocation
                position: absolute
        .right
            position: relative
            width: 50px
            height: 50px
            .rect
                position: absolute 
                top: 0
                left: 0
                bottom: 0
                background: grey 
                transition: .3s all ease
            .active
                right: 0
            .rightsvg
                position: absolute
</style>



