<template>
    <div class="one">
        <scroll class="scroll-wrapper">
            <div>
                <div class="one-top" @click="clicknews">
                    <news></news>
                </div>
                <div class="wrapper">
                    <div class="wrapper-one" >
                        <span class="main-word"></span>
                        <div class="include-arrow" @click="pictureArrowClick">
                            <span class="main-word"></span>
                            <arrow></arrow>
                        </div>
                    </div>
                    <div class="wrapper-two" ref="listion">
                        <logo></logo>
                        <span class="company-name"></span>
                        <img src="../../../static/one-img/cloud.jpg" class="bgimg"/>
                        <div class="include-arrow">
                            <span class="induct"></span>
                            <arrow></arrow>
                        </div>
                        <p v-for="item in mainWord" class="mainword">{{item}}</p>
                        <img src="../../../static/one-img/hand.jpg" class="bgimg hand"/>
                        <div class="include-arrow-2" @click="cooperateArrowClick">
                            <span class="induct"></span>
                            <arrow></arrow>
                        </div>
                        <img src="../../../static/one-img/car.jpg" class="bgimg car"/>
                        <div class="include-arrow-2"  @click="customerArrowClick">
                            <span class="induct"></span>
                            <arrow></arrow>
                        </div>
                        <img src="../../../static/one-img/test.jpg" class="bgimg test"/>
                        <div class="include-arrow-2">
                            <span class="induct"  @click="machineArrowClick"></span>
                            <arrow></arrow>
                        </div>
                        <div class="round" @click="roundClick">
                            <div v-for="item in arr" class="inner-round"></div>
                        </div>
                    </div>
                </div>
            </div>
        </scroll>
    </div>
</template>

<script type="text/ecmascript-6">
import news from 'common/svg/news'
import arrow from 'common/svg/arrow'
import logo from 'common/svg/logo'
import scroll from 'common/scroll/scroll'

export default {
    props: {

    },
    data() {
        return {
            mainWord: ["2011","3MHVDonaldson  ","TUV ISO90012008",""],
            arr: [1,2,3],
            newsflag: true
        };
    },
    computed: {

    },
    created() {
    },
    mounted() {
    },
    watch: {

    },
    methods: {
        clicknews() {
            if(this.newsflag) {
                this.$router.push('news')
                this.newsflag = !this.newsflag
            } else {
                this.$router.go(-1)
                this.newsflag = !this.newsflag
            }
        },
        pictureArrowClick() {
            this.$router.push('pictureList')
        },
        cooperateArrowClick() {
            this.$router.push('cooperate')
        },
        customerArrowClick() {
            this.$router.push('customer')
        },
        machineArrowClick() {
            this.$router.push('machine')
        },
        roundClick() {
            this.$router.push('people')
        }
    },
    components: {
        arrow,
        news,
        logo,
        scroll
    },
};
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
    @import "~common/styles/variable"
	@import "~common/styles/mixin"
    
    .one
        position: fixed
        width: 100%
        top: 0
        bottom: 0
        .scroll-wrapper
            height: 100%
            overflow: hidden
            .one-top
                // position: absolute
                float: right
                top: 0
                right: 0
            .wrapper
                padding-top: 100px
                .wrapper-one
                    margin: 100px 0 0 40px
                    .main-word
                        font-size: $f-main-sub
                        color: $c-bg-heavy
                        line-height: 3rem
                .wrapper-two
                    margin-left: 40px
                    height: auto
                    .logo
                        display: block
                        padding-top: 40px
                    .company-name
                        font-size: $f-text
                        color: $c-bg-heavy
                        line-height: 2rem
                        margin-bottom: 5px
                    .bgimg
                        // height: 600px
                        width: calc(100% - 40px)
                    .include-arrow
                        margin-top: 10px
                        display: flex
                        align-items: center
                        .main-word
                            font-size: $f-main-sub
                            color: $c-bg-heavy
                            line-height: 3rem
                        .induct
                            font-size: $f-text
                            color: $c-bg-heavy
                            line-height: 2rem
                            margin-bottom: 5px
                        .arrow
                            font-size: $f-text
                            color: $c-bg-heavy
                            line-height: 2rem
                            margin-left: 5px
                            transform: rotate(90deg)
                    .mainword
                        font-size: $f-link
                        color: $c-bg-heavy
                        width: calc(100% - 35px)
                        line-height: 1.2rem
                        margin-top: 8px
                    .hand
                        margin-top: 70px
                    .include-arrow-2
                        margin-top: 10px
                        display: flex
                        align-items: center
                        .induct
                            font-size: $f-text
                            color: $c-bg-heavy
                            line-height: 2rem
                        .arrow
                            margin-left: 10px
                            line-height: 2rem
                    .car
                        margin-top: 80px
                    .test
                        margin-top: 40px
                    .round
                        width: 100px
                        height: 100px
                        border-radius: 50%
                        border: 1px solid $c-bg-heavy
                        margin: 60px 0 60px
                        margin-left: calc(50% - 70px)
                        display: flex
                        align-items: center
                        justify-content: center
                        .inner-round
                            line-height: 100px
                            width: 3px
                            height: 3px
                            border-radius: 50%
                            border: 1px solid $c-bg-heavy
                            text-align: center
                            margin: 3px
</style>



