import Vue from 'vue'
import Router from 'vue-router'
import one from 'components/one/one'
import addresse from 'components/address/address'
import cooperate from 'components/cooperate/cooperate'
import customer from 'components/customer/customer'
import machine from 'components/machine/machine'
import people from 'components/people/people'
import pictureList from 'components/picture-list/picture-list'
import productList from 'components/product-list/product-list'
import furnace from 'components/all-prouduct/furnace'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'one',
      component: one
    },
    {
      path: '/address',
      name: 'address',
      component: addresse
    },
    {
      path: '/cooperate',
      name: 'cooperate',
      component: cooperate
    },
    {
      path: '/customer',
      name: 'customer',
      component: customer
    },
    {
      path: '/machine',
      name: 'machine',
      component: machine
    },
    {
      path: '/people',
      name: 'people',
      component: people
    },
    {
      path: '/pictureList',
      name: 'pictureList',
      component: pictureList
    },
    {
      path: '/productList',
      name: 'productList',
      component: productList
    },
    {
      path: '/furnace',
      name: 'furnace',
      component: furnace
    }
  ]
})



