<template>
    <div class="product-list">
        <div v-for="(item, index) in pList" class="pList" @click="toTheImage(index)">
            <span class="inner-word1">{{item}}</span>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
export default {
    props: {

    },
    data() {
        return {
            pList: {
                "Furnace":"AC Furnace Air Filter & HVAC",
                "Purifier":"Air Purifier Filter",
                "vacuum":"vacuum cleaner Filter",
                "car":"",
                "litter":"",
                "medcial":"",
                "test":"Test room"
            }
        };
    },
    computed: {

    },
    created() {

    },
    mounted() {

    },
    watch: {

    },
    methods: {
        toTheImage(x) {
            this.$router.push(x)
        }
    },
    components: {

    },
};
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
    @import "~common/styles/variable"
	@import "~common/styles/mixin"

    .product-list
        position: fixed
        z-index: 100
        width: 100vw
        height: calc(100vh - 200px)
        display flex
        flex-direction column
        justify-content space-between
        margin-top: 100px
        align-items center
        .pList
            height: 30px
            border-bottom: 1px solid $c-bg-heavy 
            .inner-word1
                color: $c-bg-heavy 
</style>



